.Roster {
  .slidingUnderline {
    width: 0px;
    height: 6px;
    background-color: #d79e00;
    margin-top: 20px;
    transition: all 0.25s;
  }

  h1 {
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 59px;
    /* identical to box height */

    text-transform: uppercase;

    color: #ffffff;
  }
  .TextField {
    margin: 0 auto;
  }

  ul {
    padding: 0;
    margin-top: 40px;
  }

  li {
    color: white;
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 700;
    font-size: 3em;
    text-align: left;
    list-style: none;
    background-color: black;
    margin: 0;
    padding: 40px 20px;
    transition: all 0.25s;
  }
  li:hover {
    // text-decoration: underline;
    cursor: pointer;
    color: #d79e00;
    padding: 60px 40px;
    border-left: 8px solid #d79e00;
  }

  li:hover > .slidingUnderline {
    width: 400px;
  }
}
